.App {
  position: relative;
}

.App-workspace {
  display: -ms-flexbox;
  display: flex;
}

.App-workspace-cards {
  overflow: hidden;
}

.App-offline {
  position: absolute;
  top: 0;
  right: 0;
  background-color: #e10000;
  color: #fff;
  font-weight: bold;
  padding: 0.25rem 0.5rem;
}



